<template>
	<div class="my-bank-card-set">
		<div class="account-msg">
			<p>{{card.name}}</p>
			<p>{{card.account}} | {{card.type}}</p>
			<p>{{card.bankName}}</p>
			<div class="account-type">
				<ul>
					<li>人民币卡</li>
					<li>账户类型：I类</li>
					<li>账户状态：正常</li>
				</ul>
			</div>
		</div>
		<mt-cell class="default-set" title="设置主账户">
  			<mt-button @click.native="setDefault()" type="primary" size="small">设置</mt-button>
		</mt-cell>
		<mt-cell @click.native="setName()" class="default-set" title="设置别名">
  			<span>{{!card.alias ? '未设置' : card.alias}}</span>
		</mt-cell>
		<div class="field-action">
			<mt-button @click.native="deleteCard()" size="large" type="primary">从手机银行删除此卡</mt-button>
		</div>
	</div>
</template>

<script>
import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/my/my-bank-card';

export default{
	data() {
		return {
			card: {},
			rename: ''
		};
	},
	methods: {
		setDefault() {
			var self = this;
			MessageBox({
			 	title: '您确定要将此银行卡设为主账户',
			  	message: `${self.card.bankName}<br>${self.card.account}`,
			  	showCancelButton: true
			}).then(action => {
				if(action === 'confirm'){
					service.defaultBankCard({
						id: self.card.id
					}).then(data => {
						console.log(data);
					});
				}
			});
		},
		setName() {
			this.$router.push(`/my-bank-card/card-set/set-name/${this.card.id}/${this.card.alias}`);
		},
		deleteCard() {
			var self = this;
			MessageBox({
			 	title: '您确定要将此卡从手机银行删除？',
			  	message: `${self.card.bankName}<br>${self.card.account}`,
			  	showCancelButton: true
			}).then(action => {
				if(action === 'confirm'){
					service.deleteBankCard({
						id: self.card.id
					}).then(data => {
						this.$router.push('/my-bank-card/card-delete-success');
					});
				}
			});
		},
		success() {
			history.back();
		},
		getBankCardDetail(id) {
			service.getBankCardDetail({
				id: id
			}).then(data => {
				this.card = data.data;
				console.log(this.card);
			});
		}
	},
	created(){
		this.getBankCardDetail(this.$route.params.id);
	}
};

</script>

<style lang="scss">

.my-bank-card-set{
	.account-msg{
		background-color: #fff;
		padding: 24px 16px 16px;
		>p{
			color: #000;
			font-size: 14px;
			margin-bottom: 6px;
		}
		.account-type{
			margin-top: 32px;
			>ul>li{
				display: inline-block;
				width: 32%;
			}
			>ul>li:nth-child(2){
				margin: 0 1%;
				text-align: center;
			}
			>ul>li:last-child{
				text-align: right;
			}
		}
	}
	.default-set{
		margin: 16px 0px;
	}
	.mint-cell-value > input{
		text-align: right;
	}
	.submit-success{
		position: absolute;
		width: 100%;
		height:100%;
		top: 0;
		left: 0;
		background-color: #fff;
		z-index: 1;
		text-align: center;

		.submit-contain{
			margin-top: 50%;
			img{
				width: 80px;
			}
			.p-success{
				font-size: 12px;
				margin: 20px 0px;
			}
		}

	}
}

</style>


