<template>
	<div class="my-bank-card">
		<mt-navbar fixed v-model="selected">
  			<mt-tab-item id="1">储蓄卡({{AccountList.length}})</mt-tab-item>
  			<mt-tab-item id="2">信用卡(3)</mt-tab-item>
		</mt-navbar>
		<div class="my-bank-card-contain">
			<mt-tab-container v-model="selected">
	  			<mt-tab-container-item id="1">
	    			<bank-card :bank-card-list="accountList" @bank-card-click="bankCardClick"></bank-card>
	    			<div v-show="AccountList.length > accountList.length" class="field-action">
	    				<mt-button @click.native="showMore()" type="primary">展示更多已添加的卡</mt-button>
	    			</div>
	    			<div class="field-warn">
						
	    				{{account.length > 0 ? '未添加到手机银行的账户' : '您的账号已全部添加至手机银行！'}}
					</div>
					<div v-for="item in account" class="bank-card-account">
						<p>{{item.number}}</p>
						<p>{{item.bank}}</p>
						<div class="bank-card-add">
							<div>余额： {{item.count | number(2)}}</div>
							<mt-button @click.native="addAccount(item.id)" size="small" type="primary" class="bank-card-add-btn">添加</mt-button>
						</div>
					</div>
					<div class="field-action">
						<mt-button @click.native="addBankCard" size="large" type="primary">添加银行卡</mt-button>
					</div>
	  			</mt-tab-container-item>
	  			<mt-tab-container-item id="2">
	    			<bank-card :bank-card-list="creditList" @bank-card-click="bankCardClick"></bank-card>
	    			<div class="field-action">
						<mt-button @click.native="addCredit" size="large" type="primary">+手动添加信用卡</mt-button>
					</div>
	  			</mt-tab-container-item>
			</mt-tab-container>
			
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			selected: '1',
			account: [],
			accountList: [],
	        creditList: [],
	        AccountList: [],
	        accountListNum: 10
		};
	},
	methods: {
		detail(item){
			this.$router.push(`/my-bank-card/detail/${item.id}`);
		},
		addAccount(id){
			this.$router.push(`/my-bank-card/add-account/${id}`);
		},
		addBankCard(){
			this.$router.push('/my-bank-card/add-card');
		},
		bankCardClick(item){
			this.$router.push(`/my-bank-card/card-set/${item.id}`);
		},
		getBankCardList() {
			service.getBankCardList().then(data => {
				this.AccountList = data.data;
				this.accountList = this.AccountList.slice(0, this.accountListNum);
			});
		},
		getBankCardAccount() {
			service.getBankCardAccount().then(data => {
				this.account = data.data;
			});
		},
		getBankCreditList() {
			service.getBankCreditList().then(data => {
				this.creditList = data.data;
			});
		},
		showMore(){
			this.accountListNum += 10;
			this.accountList = this.AccountList.slice(0, this.accountListNum);
		}
	},
	created(){
		this.getBankCardList();
		this.getBankCardAccount();
		this.getBankCreditList();
	}
};

</script>

<style lang="scss">

.my-bank-card{
	.mint-tab-container{
		padding-top: 16px;
	}
	.my-bank-card-contain{
		padding-top: 100px;
	}
	.bank-card{
		margin: 20px 20px 0px;
		border-radius: 5px;
	}
	.card-list > li > a{
		height: 160px;
	}
	.bank-card-account{
		padding: 20px;
		background-color: #fff;
		border-top: 1px solid #d9d9d9;

		p{
			font-size: 12px;
		}

		.bank-card-add{
			margin-top: 10px;
			div{
				display: inline-block;
				color: #000;
				font-size: 14px;
				height: 0.88rem;
				line-height: 0.88rem;
			}
			.bank-card-add-btn{
				float: right;
			}
		}
	}
	.mint-navbar .mint-tab-item.is-selected{
		margin-bottom: 0px;
	}
}

</style>


