<template>
	<div class="my-bank-card-account">
		<div class="account-msg">
			<p>{{account.name}}</p>
			<p>{{account.account}} | {{account.type}}</p>
			<p>{{account.bankName}}</p>
			<div class="account-type">
				<ul>
					<li>人民币卡</li>
					<li>账户类型：I类</li>
					<li>账户状态：正常</li>
				</ul>
			</div>
		</div>
		<mt-field class="account-rename" label="设置别名" placeholder="（选填）请输入别名，例如：（理财卡）" v-model="rename"></mt-field>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="field-action">
			<submit-button @submit="submit" :submitStatus="submitStatus" size="large" value="确认添加"></submit-button>
		</div>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			account: {},
			rename: '',
			password: '1111',
			submitStatus: true,
			cellPassword: {
                title: '交易密码',
                rule: 'number'
            }
		};
	},
	methods: {
		validate() {
			if(this.$validate.isEmpty( this.password )){
				this.submitStatus = false;
			}else{
				this.submitStatus = true;
			}
		},
		submit() {
			var _error = [];
			var self = this;
			if( this.$validate.isEmpty( this.password ) ) {
                _error.push( '交易密码不能为空。' );
            }
            if( _error.length > 0 ) {
                Toast({
                    message: _error[0],
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
            service.addBankCardAccount({
            	id: self.account.id,
            	alias: self.account.alias,
            	password: self.account.password
            }).then(data => {
            	this.$router.push('/my-bank-card/add-account-success');
            });
		},
		getBankCardDetail(id) {
			service.getBankCardDetail({
				id: id
			}).then(data => {
				this.account = data.data;
			});
		}
	},
	created(){
		this.getBankCardDetail(this.$route.params.id);
	}
};

</script>

<style lang="scss">

.my-bank-card-account{

	.account-msg{
		background-color: #fff;
		padding: 24px 16px 16px;
		>p{
			color: #000;
			font-size: 14px;
			margin-bottom: 6px;
		}
		.account-type{
			margin-top: 32px;
			>ul>li{
				display: inline-block;
				width: 32%;
			}
			>ul>li:nth-child(2){
				margin: 0 1%;
				text-align: center;
			}
			>ul>li:last-child{
				text-align: right;
			}
		}
	}
	.account-rename,.account-password{
		margin: 16px 0px;
	}
	.mint-button--default{
		background-color: #666;
		color: #fff;
	}

}

</style>


